<template>
    <div class="address-list">
        <div  v-if="tableData &&tableData.hasOwnProperty('list') && tableData.list.length>0">
            <div v-for="(item) in tableData.list" :key="item.index">
                <div class="addressList">
                    <div class="address-person-info">
                        <div>{{item.name}}</div>
                        <div>{{item.mobile}}</div>
                    </div>
                    <div class="address-detail">
                        <div class="left-item">{{item.regionsName}}{{item.address}}</div>
                    </div>
                    <div class="address-detail">
                        <div class="left-item">{{ $L('邮政编码') }}:{{item.recCode}}</div>
                    </div>
                    <div class="address-default">
                        <div class="left">
                            <el-radio v-model="item.isDefault == 1 ? '1': '0'" label="1" v-if="item.isDefault === 1">{{ $L('默认地址') }}</el-radio>
                            <el-radio v-model="item.isDefault == 0 ? '1': '0'" label="0" v-else @change="setDefaultFunc(item.id)">{{ $L('设为默认') }}</el-radio>
                        </div>
                        <div class="right">
                            <span @click="addAddressFunc(item.id)">
                              <i class="el-icon-edit-outline"></i>
                              {{ $L('编辑') }}
                            </span>
                            <span @click="delMsgFunc(item.id)">
                              <i class="el-icon-delete"></i>
                              {{ $L('删除') }}
                            </span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="list-pages" v-if="tableData.list !== null && tableData.allNum>0 &&  tableData.allNum>pageSize">
                <el-pagination
                        background
                        small
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-size="pageSize"
                        layout="prev, pager, next"
                        :total="tableData.allNum">
                </el-pagination>
            </div>
        </div>
        <div class="no-data" v-else>
            {{ $L('暂无数据') }}
        </div>
        <div class="add-newAdd">
            <el-button type="primary" icon="el-icon-plus" @click="addAddressFunc()" >{{ $L('添加新地址') }}</el-button>
        </div>
    </div>
</template>

<script>
    import addAddress from "../../../components/home/addAddress.vue";
    export default {
        name: "addressList",
        components: {
            addAddress
        },
        data() {
            return {
                loading: true,
                errorType: "error",
                errorTitle: this.$L("出错了"),
                errorTips: null,
                errorShow: false,
                addressNum: 6,
                allAddressNum: 20,
                inputValue: "",
                tableData: {},
                pageSize: 10,
                currentPage: 1,
                addressArr: [],
                addressId: 0
            };
        },
        created() {
            this.getData();
        },
        methods: {
            showError(title, tips, type) {
                if (type === "warning") {
                    this.errorType = "warning";
                } else {
                    this.errorType = "error";
                }
                if (title && title !== "") {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L("出错了");
                }
                if (tips && tips !== "") {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData() {
                const _self = this;
                _self.loading = true;
                let arr = {
                    p: _self.currentPage,
                    num: _self.pageSize,
                    keyword: _self.inputValue
                };
                _self.$api(
                    "getAddressList",
                    arr,
                    function(reData) {
                        if (reData.flag === 1) {
                            _self.tableData = reData.data;
                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self
                                .$confirm(_self.$L("请重新登录"), _self.$L("登录超时"), {
                                    confirmButtonText: _self.$L("重新登录"),
                                    cancelButtonText: _self.$L("取消"),
                                    type: "error",
                                    distinguishCancelAndClose: true
                                })
                                .then(() => {
                                    _self.$router.push({
                                        name: "login",
                                        query: { redirect: _self.$route.fullPath }
                                    });
                                })
                                .catch(action => {
                                    _self.$gotoWebHome();
                                });
                        } else {
                            //未知错误
                            _self
                                .$confirm(_self.$L("数据加载失败"), _self.$L("出错了"), {
                                    confirmButtonText: _self.$L("点击重试"),
                                    cancelButtonText: _self.$L("返回首页"),
                                    type: "error",
                                    distinguishCancelAndClose: true
                                })
                                .then(() => {
                                    _self.getData();
                                })
                                .catch(action => {
                                    _self.$router.push({name: 'home'});
                                });
                        }
                    },
                    function(error) {
                        //网络请求错误
                        _self
                            .$confirm(_self.$L("网络请求失败"), _self.$L("出错了"), {
                                confirmButtonText: _self.$L("点击重试"),
                                cancelButtonText: _self.$L("返回首页"),
                                type: "error",
                                distinguishCancelAndClose: true
                            })
                            .then(() => {
                                _self.getData();
                            })
                            .catch(action => {
                                _self.$router.push({name: 'home'});
                            });
                    }
                );
            },

            handleCurrentChange(val) {
                this.currentPage = val;
                this.getData();
            },
            handleSizeChange(val) {
                this.pageSize = val;
                this.getData();
            },
            searchFunc(val) {
                this.inputValue = val;
                this.getData();
            },
            handleSelectionChange(val) {
                let arr = [];
                for (let i in val) {
                    arr[i] = val[i].id;
                }
                this.addressArr = arr;
                console.log(this.addressArr);
            },
            delMsgFunc(id) {
                const _self = this;
                if (!id && (Array.isArray(this.msgArr) && this.msgArr.length === 0)) {
                    //未知错误
                    _self
                        .$confirm(
                            _self.$L("您还没有选中地址信息，请勾选地址再做此操作"),
                            _self.$L("出错了"),
                            {
                                confirmButtonText: _self.$L("点击重试"),
                                cancelButtonText: _self.$L("返回首页"),
                                type: "warning",
                                distinguishCancelAndClose: true
                            }
                        )
                        .then(() => {
                            _self.getData();
                        })
                        .catch(action => {
                            _self.$gotoWebHome();
                        });
                    return;
                }

                let addId;

                if (id) {
                    addId = id;
                } else {
                    addId = this.addressArr;
                }

                let arr = {
                    idRec: addId
                };
                _self
                    .$confirm(
                        _self.$L("此操作将永久删除该地址信息, 是否继续?"),
                        _self.$L("提示"),
                        {
                            confirmButtonText: _self.$L("确定"),
                            cancelButtonText: _self.$L("取消"),
                            type: "warning"
                        }
                    )
                    .then(() => {
                        _self.$api(
                            "deleteAddress",
                            arr,
                            function(reData) {
                                if (reData.flag === 1) {
                                    _self.loading = false;
                                    _self.$message({
                                        type: "success",
                                        message: _self.$L("删除成功!")
                                    });
                                    _self.getData();
                                } else if (reData.flag === -1) {
                                    //登录超时了
                                    _self
                                        .$confirm(_self.$L("请重新登录"), _self.$L("登录超时"), {
                                            confirmButtonText: _self.$L("重新登录"),
                                            cancelButtonText: _self.$L("取消"),
                                            type: "error",
                                            distinguishCancelAndClose: true
                                        })
                                        .then(() => {
                                            _self.$router.push({
                                                name: "login",
                                                query: { redirect: _self.$route.fullPath }
                                            });
                                        })
                                        .catch(action => {
                                            _self.$gotoWebHome();
                                        });
                                } else {
                                    console.log(reData);
                                    //未知错误
                                    _self
                                        .$confirm(_self.$L("数据加载失败"), _self.$L("出错了"), {
                                            confirmButtonText: _self.$L("点击重试"),
                                            cancelButtonText: _self.$L("返回首页"),
                                            type: "error",
                                            distinguishCancelAndClose: true
                                        })
                                        .then(() => {
                                            _self.getData();
                                        })
                                        .catch(action => {
                                            _self.$gotoWebHome();
                                        });
                                }
                            },
                            function(error) {
                                console.log(error);
                                //网络请求错误
                                _self
                                    .$confirm(_self.$L(error.msg), _self.$L("出错了"), {
                                        //todo:msg还未更换语言包语言包
                                        confirmButtonText: _self.$L("点击重试"),
                                        cancelButtonText: _self.$L("返回首页"),
                                        type: "error",
                                        distinguishCancelAndClose: true
                                    })
                                    .then(() => {
                                        _self.getData();
                                    })
                                    .catch(action => {
                                        _self.$gotoWebHome();
                                    });
                            }
                        );
                    })
                    .catch(() => {});
            },
            setDefaultFunc(id) {
                const _self = this;
                _self.addressId = id;
                _self.loading = true;
                if (id !== null) {
                    _self.$api(
                        "setDefaultAddress",
                        { idRec: _self.addressId },
                        function(reData) {
                            if (reData.flag === 1) {
                                _self.$message({
                                    type: 'success',
                                    message: _self.$L('设置默认地址成功!')
                                });
                                _self.getData();
                                _self.loading = false;
                            } else if (reData.flag === -1) {
                                //登录超时了
                                _self
                                    .$confirm(_self.$L("请重新登录"), _self.$L("登录超时"), {
                                        confirmButtonText: _self.$L("重新登录"),
                                        cancelButtonText: _self.$L("取消"),
                                        type: "error",
                                        distinguishCancelAndClose: true
                                    })
                                    .then(() => {
                                        _self.$router.push({
                                            name: "login",
                                            query: { redirect: _self.$route.fullPath }
                                        });
                                    })
                                    .catch(action => {
                                        _self.$gotoWebHome();
                                    });
                            } else {
                                //未知错误
                                _self
                                    .$confirm(_self.$L("数据加载失败"), _self.$L("出错了"), {
                                        confirmButtonText: _self.$L("点击重试"),
                                        cancelButtonText: _self.$L("返回首页"),
                                        type: "error",
                                        distinguishCancelAndClose: true
                                    })
                                    .then(() => {
                                        _self.getData();
                                    })
                                    .catch(action => {
                                        _self.$gotoWebHome();
                                    });
                            }
                        },
                        function(error) {
                            //网络请求错误
                            _self
                                .$confirm(_self.$L("网络请求失败"), _self.$L("出错了"), {
                                    confirmButtonText: _self.$L("点击重试"),
                                    cancelButtonText: _self.$L("返回首页"),
                                    type: "error",
                                    distinguishCancelAndClose: true
                                })
                                .then(() => {
                                    _self.getData();
                                })
                                .catch(action => {
                                    _self.$gotoWebHome();
                                });
                        }
                    );
                }
            },
            addAddressFunc(id) {
                if(id){
                    this.$router.push({name:'addAddress',params:{id:id}});
                } else {
                    if(this.tableData.allNum < 20){
                        this.$router.push({name:'addAddress'});
                    } else {
                        this.$message({
                            type: "error",
                            message: this.$L("最多可添加20条地址信息!")
                        });
                    }
                }
            }
        }
    };
</script>

<style lang="scss" scoped>
    .address-list {
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;
        margin-bottom: 65px;
        background-color: #f2f2f2;
        .addressList {
            background-color: #fff;
            margin-top: 10px;
            .address-person-info {
                div {
                    display: inline-block;
                    margin-left: 15px;
                    padding: 6px 0;
                }
            }
            .address-detail {
                padding: 5px 15px;
                .left-item{
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }
            }
            .address-default {
                padding: 10px 0;
                height: 46px;
                line-height: 24px;
                border-top: 1px solid #ddd;
                border-bottom: 1px solid #ddd;
                .left {
                    float: left;
                    margin-left: 10px;
                }
                .right {
                    float: right;
                    span {
                        display: inline-block;
                        padding: 0 10px;
                        margin-right: 10px;
                    }
                }
            }
        }
        .list-pages{
            text-align: center;
            padding: 10px 0;
        }
        .no-data{
            padding: 0 10px;
            background: #fff;
            height: 50px;
            line-height: 50px;
            text-align: center;
            color: #999;
            margin-top: 10px;
        }
        .add-newAdd{
            position: fixed;
            bottom: 0;
            width: 100%;
            background: #fff;
            z-index: 100;
            border-top: 1px solid #F2F6FC;
            text-align: center;
            padding: 8px 0;
            text-align: center;
            .el-button{
                width: calc(100% - 20px);
            }
        }
    }
</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>